<template>
  <div id="view-item">
    <h3>View Item</h3>
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Item ID: {{item_id}}</li>
      <li class="collection-item">Item Description: {{description}}</li>
      <li class="collection-item">Item Price: {{price}}</li>
    </ul>
    <router-link to="/" class="btn grey">Return</router-link>
    <button @click="deleteItem" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-item', params: {item_id: item_id}}"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "view-item",
  data() {
    return {
      item_id: null,
      name: null,
      description: null,
      price: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("items")
      .where("item_id", "==", to.params.item_id)
      .get()
      .then((querySnaphot) => {
        querySnaphot.forEach((doc) => {
          next((vm) => {
            vm.item_id = doc.data().item_id;
            vm.name = doc.data().name;
            vm.description = doc.data().description;
            vm.price = doc.data().price;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("items")
        .where("item_id", "==", this.$route.params.item_id)
        .get()
        .then((querySnaphot) => {
          querySnaphot.forEach((doc) => {
            this.item_id = doc.data().item_id;
            this.name = doc.data().name;
            this.description = doc.data().description;
            this.price = doc.data().price;
          });
        });
    },
    deleteItem() {
      if (confirm("Are you sure?")) {
        db.collection("items")
          .where("item_id", "==", this.$route.params.item_id)
          .get()
          .then((querySnaphot) => {
            querySnaphot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    },
  },
};
</script>


