<template>
  <div id="edit-item">
    <h3>Edit Item</h3>
    <div class="row">
      <form @submit.prevent="updateItem" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="item_id" disabled />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="description" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="price" required />
          </div>
        </div>
        <button type="submit" class="btn green">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "edit-item",
  data() {
    return {
      item_id: null,
      name: null,
      description: null,
      price: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("items")
      .where("item_id", "==", to.params.item_id)
      .get()
      .then((querySnaphot) => {
        querySnaphot.forEach((doc) => {
          next((vm) => {
            vm.item_id = doc.data().item_id;
            vm.name = doc.data().name;
            vm.description = doc.data().description;
            vm.price = doc.data().price;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("items")
        .where("item_id", "==", this.$route.params.item_id)
        .get()
        .then((querySnaphot) => {
          querySnaphot.forEach((doc) => {
            this.item_id = doc.data().item_id;
            this.name = doc.data().name;
            this.description = doc.data().description;
            this.price = doc.data().price;
          });
        });
    },
    updateItem() {
      db.collection("items")
        .where("item_id", "==", this.$route.params.item_id)
        .get()
        .then((querySnaphot) => {
          querySnaphot.forEach((doc) => {
            doc.ref
              .update({
                item_id: this.item_id,
                name: this.name,
                description: this.description,
                price: this.price,
              })
              .then(() => {
                this.$router.push({
                  name: "view-item",
                  params: { item_id: this.item_id },
                });
              });
          });
        });
    },
  },
};
</script>


